@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
html {
  height: 100vh;
  width: 100vw;
}
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  line-height: 1.5em;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

button {
  font-size: 1.1rem;
  background-color: black;
  color: white;
  border: none;
  border-radius: 4px;
  width: 10rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
button:hover {
  cursor: pointer;
}
button:hover,
button:focus {
  opacity: 0.8;
}
button:disabled {
  background-color: lightgrey;
}
button:disabled:hover {
  opacity: 1;
  cursor: inherit;
}

#root,
.App {
  height: 100%;
  width: 100%;
}

.App {
  padding: 1rem;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
}

.App h1 {
  margin-bottom: 2rem;
}

.App__Shelf {
  display: grid;
  width: 100%;
  height: 100%;
  max-width: 1000px;
  max-height: 1000px;
  margin: 0 auto;
  grid-gap: 1rem;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
}

.App__Item {
  border: 2px solid black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-transition: box-shadow 300ms ease;
  transition: box-shadow 300ms ease;
  padding: 1rem;
  min-width: 0;
}
.App__Item.\--editOptions {
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.App__Item.\--editOptions:hover {
  cursor: inherit;
}
.App__Item.\--editOptions li:hover {
  cursor: pointer;
}
.App__Item li {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.App__Item li p {
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.App__Item.\--empty {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.App__Item:hover {
  cursor: pointer;
}
.App__Item:hover,
.App__Item:focus {
  box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
}

.App__AddItem {
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0 1rem;
  width: 100%;
}

.App__AddItem input {
  width: 100%;
  border: 2px solid grey;
  border-radius: 4px;
  margin-bottom: 1rem;
  padding: 0.5rem;
}

.Modal {
  position: absolute;
  z-index: 10;
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  margin: -1rem;
}

.Modal__Header {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 1rem 0 1rem;
}
.Modal__Close {
  -webkit-align-self: flex-start;
          align-self: flex-start;
}
.Modal__Close:hover {
  cursor: pointer;
}

.Modal__inner {
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 30rem;
  height: 25rem;
  overflow-y: auto;
}

.App__ConfirmDelete {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
}
.App__ConfirmDelete p {
  width: 100%;
  text-align: center;
  font-size: 2rem;
  margin: 0;
  font-weight: bold;
}

.App__ConfirmDelete__Confirm {
  background-color: red;
}

.Error {
  position: absolute;
  z-index: 11;
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: -1rem;
}

.Error span {
  background-color: red;
  color: white;
  border-radius: 2px;
  padding: 1rem;
}

.App__Pagination {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.App__Pagination > span {
  width: 100%;
}

.App__Pagination > button {
  width: 30%;
}

.App__left {
  width: 15rem;
}

